export function useSuggestionFetch(baseURL = 'https://rdc-api-catalog-gateway-api.rakuten.co.jp/gsp-ac/autocomplete/v1/autocomplete.jp_websearch_page',
                                   SID = 'jp_websearch_ac_page_004') {
  const cachedData = new window.Map()
  const mutex = new window.Map()

  async function process(q) {
    if (cachedData.get(q)) {
      return cachedData.get(q)
    } else {
      if (!mutex.has(q)) {
        mutex.set(q, fetch(`${baseURL}?sid=${SID}&q=${encodeURIComponent(q)}&format=json`)
          .then(resp => resp.json())
          .then(data => data.suggestions.map(e => e.terms))
          .then(suggestions => {
            cachedData.set(q, suggestions)
            mutex.delete(q)
            return suggestions
          }));
      }
      return await mutex.get(q)
    }
  }

  return {
    process
  }
}
