import React, { Fragment } from 'react'

export function boldText(textToBold, fullText) {
  const startIndex = fullText.toLowerCase().indexOf(textToBold.toLowerCase())
  if (startIndex === -1) {
    return fullText
  }
  const endIndex = startIndex + textToBold.length
  return (
    <Fragment>
      {fullText.slice(0, startIndex)}<strong>{textToBold}</strong>{fullText.slice(endIndex)}
    </Fragment>
  )
}
