import React, { useCallback } from 'react'
import './suggestionBox.css'
import * as PropTypes from 'prop-types'
import { boldText } from './utils'
import { useSuggestionFetch } from './fetch'
import { useSuggestionHistory } from './suggestionHistory'

import useDetectClickOutside from './hooks/useDetectClickOutside'
import useHistoryData from './hooks/useHistoryData'
import useKeyData from './hooks/useKeyData'

const fetch = useSuggestionFetch()
const history = useSuggestionHistory()

function SuggestionBox({ input }) {
  const container = document.getElementById('search-suggestion-container')
  const { historyData, handleRemoveHistory } = useHistoryData(history.get, history.clear)
  const { isShow, setIsShow, keyword, hover,
    isDefaultHistory, relatedHistory, source } = useKeyData(input, container, fetch.process, historyData)
  const wrapperRef = useDetectClickOutside(() => setIsShow(false))

  const handleSearchRedirect = useCallback((q) => {
    input.value = q;
    input.focus();
    container.parentElement.getElementsByTagName('button')[0].click();
  }, [input, container]);

  if (!isShow || source.length === 0) {
    return null
  }

  return (
    <ul ref={wrapperRef}>
      {source.map((item, index) => (
        <li key={index} className={`${hover === index ? 'hovered' : ''}`}>
          {
            (isDefaultHistory || index < relatedHistory.length) ?
              <img src={"/img/suggestion_history.svg"} alt="" onClick={() => handleSearchRedirect(item)} /> :
              <img src={"/img/suggestion_search.svg"} alt="" onClick={() => handleSearchRedirect(item)} />
          }
          <span className="text" onClick={() => handleSearchRedirect(item)}>{boldText(keyword, item)}</span>
          {
            (isDefaultHistory || index < relatedHistory.length) ?
              <img src={"/img/suggestion_close.svg"} alt="" onClick={() => handleRemoveHistory(item)} /> :
              <img src={"/img/suggestion_arrow.svg"} alt="" onClick={() => handleSearchRedirect(item)} />
          }
        </li>
      ))}
    </ul>
  )
}

SuggestionBox.propTypes = {
  input: PropTypes.any
}

export default SuggestionBox
