import { useState, useEffect, useRef } from 'react'

export default function useKeyData(input, container, callAPI, historyData) {
  const [isShow, setIsShow] = useState(false)
  const [keyword, setKeyword] = useState('')
  const [hover, setHover] = useState(-1)
  const [data, setData] = useState([])
  const debounceTimer = useRef(null)

  const isDefaultHistory = (!keyword || keyword.length === 0)
  const relatedHistory = !historyData ? [] : historyData.filter(e => e.toLowerCase().includes(keyword.toLowerCase())).slice(0, 3)
  const dataWithRelatedHistory = [...relatedHistory, ...data].slice(0, 10)
  const source = isDefaultHistory ? historyData : dataWithRelatedHistory

  useEffect(() => {
    clearTimeout(debounceTimer.current)
    if (keyword && keyword.length > 0) {
      debounceTimer.current = setTimeout(async() => {
        const data = await callAPI(keyword)
        setData(data)
      }, 300)
    }
  }, [keyword, callAPI])

  useEffect(() => {
    const onClick = event => {
      setKeyword(event.target.value)
      setHover(-1)
      setIsShow(true)
    }
    input.addEventListener('click', onClick)

    const onInput = event => {
      setKeyword(event.target.value)
      setHover(-1)
      setIsShow(true)
    }
    input.addEventListener('input', onInput)

    const onMouseOver = () => {
      setHover(-1)
    }
    container.addEventListener('mouseover', onMouseOver)

    return () => {
      input.removeEventListener('click', onClick)
      input.removeEventListener('input', onInput)
      container.removeEventListener('mouseover', onMouseOver)
    }
  }, [input])

  useEffect(() => {
    const max = source.length - 1
    const onKeyDown = (event) => {
      if (event.keyCode === 38 || event.keyCode === 40) {
        event.preventDefault()

        if (event.keyCode === 38 || event.keyCode === 9) { // Arrow up key | Tab key
          setHover(h => h <= 0 ? max : h - 1)
        } else if (event.keyCode === 40) { // Arrow down key
          setHover(h => h >= max ? 0 : h + 1)
        }
      }
    }
    input.addEventListener('keydown', onKeyDown)

    return () => {
      input.removeEventListener('keydown', onKeyDown)
    }
  }, [input, source])

  useEffect(() => {
    if (hover !== -1) {
      input.value = source[hover]
    }
  }, [input, hover])

  useEffect(() => {
    container.style.display = (source.length !== 0 && isShow) ? 'block' : 'none'
  }, [input, isShow, source])

  return { isShow, setIsShow, keyword, hover, isDefaultHistory, relatedHistory, source }
}
