// new SuggestionBox('searchBoxPopup').init()

function init(searchInputId) {
	const input = document.getElementById(searchInputId);
	const containerDiv = document.createElement('div');
	containerDiv.classList.add("search-suggestion-container");
	containerDiv.id = "search-suggestion-container";
	input.parentElement.appendChild(containerDiv)
	render(<SuggestionBox input={input}/>, containerDiv);
}

init('searchBoxPopup');
