import { webSearchHistory } from '../websearchhistory'

// Bridge with the old 'websearchhistory'
export function useSuggestionHistory() {
  function get() {
    return webSearchHistory.getHistoryWords(5)
  }
  function add(word) {
    webSearchHistory.addNewWord(word)
  }

  function clear(word) {
    webSearchHistory.clearSingleHistory(word)
  }

  return {
    get,
    add,
    clear
  }
}
