// Because the current bundler of 'autocomplete' package doesn't have CSS compiler
function insertCSS(css) {
    const style = document.createElement('style')
    style.textContent = css
    document.head.appendChild(style)
}

insertCSS(`
.search-suggestion-container {
    position: absolute;
    top: calc(100% - 1px);
    left: -2px;
    z-index: 999;
    border: 2px solid #00A0F0;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
    width: 100%;
    background-color: #fff;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.search-suggestion-container ul {
    list-style: none;
    padding: 0;
    margin: 5px 0 0 0;
}

.search-suggestion-container li {
    cursor: pointer;
    padding: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-family: "Noto Sans JP", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
    font-size: 12px;
}

.search-suggestion-container li:hover, li.hovered {
    background-color: #ccc;
}

.search-suggestion-container li > span {
    width: 100%;
    padding: 0 10px;
    display: inline-block;
    white-space: nowrap;
    overflow: hidden !important;
    text-overflow: ellipsis;
}
`)
