import { useState, useEffect } from 'react'

export default function useHistoryData(historyFetch, historyRemove) {
  const [historyData, setHistoryData] = useState([])

  useEffect(() => {
    const timeout = setTimeout(() => {
      setHistoryData(historyFetch)
    }, 300)

    return () => {
      clearTimeout(timeout)
    }
  }, [])

  const handleRemoveHistory = (q) => {
    historyRemove(q)
    setTimeout(() => {
      setHistoryData(historyFetch())
    }, 200)
  }

  return { historyData, handleRemoveHistory }
}
